
export const getPokemons = async() => {

    const url = `https://sh3rlock7.github.io/pokedexAPI/db.json`
    const res = await fetch( url );
    const data = await res.json()

    const pokemones = data.pokemons.map(  pokemon =>{
        return{
            id: pokemon.id,
            numPokedex: pokemon.numPokedex ,
            nombre: pokemon.nombre,
            tipo1: pokemon.tipo1,
            tipo2: pokemon.tipo2,
        }
    
    })

    return pokemones;

}

